:root{--body-background:#eee;--body-background:var(--white);--primary:#ab47bc;--topbar:#4a14a7;--transparent-secondary:rgba(74,20,140,0.8);--white:#eee}*,:after,:before{-webkit-box-sizing:inherit;box-sizing:inherit;margin:0;padding:0}html{-webkit-box-sizing:border-box;background:var(--white);background:#eee;background:var(--body-background);box-sizing:border-box;font-size:100%;height:100%;width:100%}body{color:#4a4a4a;font-family:Lucida Grande,sans-serif;font-size:1em;line-height:1.5;margin:0;padding:0}body,html{min-width:320px}img{height:auto;max-width:100%}.container{display:block;margin:0 auto;max-width:1200px}


