h1{font-size:1.3rem;margin:.75rem 0}.hero-bg{-webkit-transform:translateY(-60px);-webkit-transition:all .5s cubic-bezier(.25,.46,.45,.94);background:url(https://andreasonny83.github.io/rhythmic-excellence-react/img/home_background.png);background-position:bottom;background-repeat:no-repeat;background-size:cover;min-height:16rem;opacity:0;padding:6rem 1rem;text-align:center;-ms-transform:translateY(-60px);transform:translateY(-60px);-o-transition:all .5s cubic-bezier(.25,.46,.45,.94);transition:all .5s cubic-bezier(.25,.46,.45,.94)}.home-link{color:var(--white);left:2.25rem;position:absolute;text-decoration:none;top:1rem}.home-link:hover{opacity:.75}.hero-logo{-webkit-transform:translateY(60px);-webkit-transition:all 1s cubic-bezier(.25,.46,.45,.94) .25s;margin-bottom:4%;opacity:0;-ms-transform:translateY(60px);transform:translateY(60px);-o-transition:all 1s cubic-bezier(.25,.46,.45,.94) .25s;transition:all 1s cubic-bezier(.25,.46,.45,.94) .25s}.hero-bg.show,.hero-logo.show{-webkit-transform:translateY(0);opacity:1;-ms-transform:translateY(0);transform:translateY(0)}@media (min-width:420px){h1{font-size:1.6rem}}@media (min-width:600px){h1{font-size:2rem}}


